div.navigation {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	position: relative;
	text-align: center;
	margin: 0 auto;
	overflow-x: scroll;
}
div.navigation::-webkit-scrollbar {
	display: none;
  }

.navigation > a {
	padding: 0px 20px;
	opacity: 0.5;
}

.navigation > a:hover {
	padding: 0px 20px;
	opacity: 0.75;
}